export default "(function () {\n\tif (typeof self === 'undefined' || !self.Prism || !self.document) {\n\t\treturn;\n\t}\n\n\tif (!Prism.plugins.toolbar) {\n\t\tconsole.warn('Copy to Clipboard plugin loaded before Toolbar plugin.');\n\n\t\treturn;\n\t}\n\n\tvar ClipboardJS = window.ClipboardJS || undefined;\n\n\tif (!ClipboardJS && typeof require === 'function') {\n\t\tClipboardJS = require('clipboard');\n\t}\n\n\tvar callbacks = [];\n\n\tif (!ClipboardJS) {\n\t\tvar script = document.createElement('script');\n\t\tvar head = document.querySelector('head');\n\n\t\tscript.onload = function () {\n\t\t\tClipboardJS = window.ClipboardJS;\n\n\t\t\tif (ClipboardJS) {\n\t\t\t\twhile (callbacks.length) {\n\t\t\t\t\tcallbacks.pop()();\n\t\t\t\t}\n\t\t\t}\n\t\t};\n\n\t\tscript.src = 'https://cdnjs.cloudflare.com/ajax/libs/clipboard.js/2.0.0/clipboard.min.js';\n\t\thead.appendChild(script);\n\t}\n\n\tPrism.plugins.toolbar.registerButton('copy-to-clipboard', function (env) {\n\t\tvar linkCopy = document.createElement('button');\n\t\tvar clip = null\n\t\tlinkCopy.textContent = 'Copy';\n\t\t//add\n\t\tPrism.hooks.add(\"complete\", function (event) { ClipboardJS && registerClipboard(event) });\n\n\t\tif (!ClipboardJS) {\n\t\t\tcallbacks.push(registerClipboard);\n\t\t} else {\n\t\t\tregisterClipboard();\n\t\t}\n\n\t\treturn linkCopy;\n\n\t\tfunction registerClipboard(event) {\n\t\t\ttry { clip && clip.destroy() } catch (e) { console.log(e) }\n\t\t\tclip = new ClipboardJS(linkCopy, {\n\t\t\t\t'text': function () {\n\t\t\t\t\treturn (event || env).code;\n\t\t\t\t}\n\t\t\t});\n\n\t\t\tclip.on('success', function () {\n\t\t\t\tlinkCopy.textContent = 'Copied!';\n\n\t\t\t\tresetText();\n\t\t\t});\n\t\t\tclip.on('error', function () {\n\t\t\t\tlinkCopy.textContent = 'Press Ctrl+C to copy';\n\n\t\t\t\tresetText();\n\t\t\t});\n\t\t}\n\n\t\tfunction resetText() {\n\t\t\tsetTimeout(function () {\n\t\t\t\tlinkCopy.textContent = 'Copy';\n\t\t\t}, 5000);\n\t\t}\n\t});\n})();\n";